.play{

    width: 100%;

    position: relative;
    margin-top: 3.125rem;

    display: flex;
    flex-direction: column;

    .triangle{
        position: absolute;
        align-self: center;
        margin-top: 4.375rem;
    }

    .items{
        display: flex;
        justify-content: center;

        .icon{
            height: 160px;
            width: 160px;
            border: 18px solid;
            border-radius: 50%;
            transition: transform .2s;

            &--paper{
                // @include icon($imageP,$paper-gradient);
                background-color: #fff;
                background-image: url($imageP);
                background-position: center;
                background-repeat: no-repeat;
                border-color: $paper-gradient;
                box-shadow: inset 0px 5px 6px grey, 
                0px 8px darken($paper-gradient, 15%);
                transform: translateX(1.5rem);
                
                &:hover{
                transform: translateX(1.5rem) scale(1.1);

                }

            }
            &--scissors{
                // @include icon($imageS,$scissors-gradient);
                background-color: #fff;
                background-image: url($imageS);
                background-position: center;
                background-repeat: no-repeat;
                border-color: $scissors-gradient;
                box-shadow: inset 0px 5px 6px grey, 
                0px 8px darken($scissors-gradient, 15%);
                transform: translateX(8rem);

                &:hover{
                    transform: translateX(8rem) scale(1.1);


                }
            }
            &--rock{
                // @include icon($imageR,$rock-gradient);
                background-color: #fff;
                background-image: url($imageR);
                background-position: center;
                background-repeat: no-repeat;
                border-color: $rock-gradient;
                box-shadow: inset 0px 5px 6px grey, 
                0px 8px darken($rock-gradient, 15%);
                transform: translateX(-10rem) translateY(13rem);
            

                &:hover{
                    transform: translateX(-10rem) translateY(13rem) scale(1.1);

                }
            
            }
        }
    }
}